.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mt-50{
  margin-top: 50px;
}
span{
  padding: 10px 20px;
  margin: 10px;
  cursor: pointer;
}
.active{
  border: solid 3px black;
}
#content{
  padding: 10px;
  border: solid 3px red;
}

.mt-15{
  margin-top: 15px;
}
.btn{
  width: 100px;
  height: 35px;
}
.dp{
  display: flex;

}
.ml-5{
  margin-left: 5px;
}
.l{
  float: right;
}
.fa-check{
  margin-left: 10px;
}